import React, { useEffect, useRef, useState } from "react";
import classes from "./BusinessAccountDetail.module.css";
import Modal2 from "../../Components/UI/Modal2/Modal2";
import styled, { css } from "styled-components";
import { updateObject } from "../../Shared/Utility";
import Input from "../../Components/UI/Input/Input";
import DropDown from "../../Components/UI/DropDown/Dropdown";
import Axios from "axios";
import MapContainer from "../AccountSetupLocation/Map/MapContainer/MapContainer";
import {
  getBusinessInformation,
  updateGeneralDetail,
  updateBusinessAddress,
  updateTagging_Timezone,
} from "../../api/Business";
import CustomButton from "../../Components/UI/Button/CustomButton";
import firebase from "../../firebase";

import { useToastMessage } from "../../contexts/ToastMessageContext";
import { useBusinessProfile } from "../../contexts/BusinessProfileContext";

const Toggler = styled.div`
  height: 0px;
  opacity: 0;
  transform: translateY(-100%);
  pointer-events: none;

  & form > div,
  & > aside {
    margin-top: 0px;
  }

  ${(props) =>
    props.open &&
    css`
      overflow-y: hidden;
      pointer-events: all;
      height: 100%;
      opacity: 1;
      transform: translateY(0%);

      & form > div,
      & > aside {
        margin-top: 24px;
      }

      & form button {
        margin-top: 5%;
        margin-left: 0;
      }
    `};
`;

const GeneralDetail = (props) => {
  const { setSuccessMessage, setErrorMessage, isDisabled, setDisabled } =
    useToastMessage();

  const { fetchDefaultBusiness, getAllUserBusiness } = useBusinessProfile();

  const [userData, setUserData] = useState({
    business_name: {
      value: "",
      elementType: "text",
      required: true,
      type: "text",
      placeholder: "Business Name",
      id: 1,
    },
    logo: {
      value: null,
      elementType: "file",
      required: false,
      type: "file",
      placeholder: "Attach Logo",
      id: 2,
    },
    industry: {
      value: "",
      elementType: "dropdown",
      required: true,
      type: "Industry",
      placeholder: "Industry",
      id: 3,
      list: [
        "Agriculture And Allied Industries",
        "Auto Componentss",
        "Automobiles",
        "Aviation",
        "Banking",
        "Cement",
        "Consumer Durables",
        "Ecommerce",
        "Education And Training",
        "Engineering And Capital Goods",
        "Financial Services",
        "FMCG",
        "Gems And Jewellery",
        "Healthcare",
        "Infrastructure",
        "Insurance",
        "IT & BPM",
        "Manufacturing",
        "Media And Entertainment",
        "Metals And Mining",
        "Oil And Gas",
        "Pharmaceuticals",
        "Ports",
        "Power",
        "Railways",
        "Real Estate",
        "Renewable Energy",
        "Retail",
        "Roads",
        "Science And Technology",
        "Services",
        "Steel",
        "Telecommunications",
        "Textiles",
        "Tourism And Hospitality",
      ],
    },
    website: {
      value: "",
      elementType: "text",
      required: true,
      type: "Website",
      placeholder: "Website URL",
      id: 4,
    },
    number: {
      value: "",
      elementType: "number",
      required: true,
      type: "number",
      placeholder: "Business Contact Number",
      id: 2,
      minLength: 10,
    },
  });

  useEffect(() => {
    if (props.data) {
      for (let key in userData) {
        if (props.data[key] !== undefined) {
          setUserData((state) => ({
            ...state,
            [key]: {
              ...state[key],
              value: props.data[key],
            },
          }));
        }
      }
    }
  }, [props.data]);

  const inputChangedHandler = (event, inputIdentifier) => {
    let updatedFormElement;

    if (event.target.type !== "file") {
      updatedFormElement = updateObject(userData[inputIdentifier], {
        value: event.target.value,
      });
    } else {
      updatedFormElement = updateObject(userData[inputIdentifier], {
        value: event.target.files,
      });
    }

    const updatedOrderForm = updateObject(userData, {
      [inputIdentifier]: updatedFormElement,
    });

    setUserData(updatedOrderForm);
  };

  const handleIndustryValue = (value) => {
    let updatedFormElement;

    updatedFormElement = updateObject(userData.industry, {
      value: value,
    });

    const updatedOrderForm = updateObject(userData, {
      industry: updatedFormElement,
    });

    setUserData(updatedOrderForm);
  };

  const uploadHandler = (callback) => {
    setDisabled();
    // folder name of the firebase storage
    let bucketName = "business-logo";

    // This is the detail of photo which is being stored
    let file;
    if (typeof userData.logo.value !== "string") {
      file = userData.logo.value[0];

      let storageRef = firebase.storage().ref(`${bucketName}/${file.name}`);

      storageRef
        .put(file)
        .then((path) => {
          storageRef.getDownloadURL().then((url) => {
            callback(url);
          });
        })
        .catch((err) => {
          props.error(err);
          setErrorMessage(err);
        });
    } else {
      callback(userData.logo.value);
    }
  };

  const formElementsArray = [];
  for (let key in userData) {
    formElementsArray.push({
      id: key,
      config: userData[key],
    });
  }

  const attachedClasses = [classes.GeneralDetail, classes.Dropdown_Container];

  return (
    <div className={attachedClasses.join(" ")}>
      <div className={classes.Dropdown_Header}>
        <h1>General Details</h1>
        <p onClick={() => props.setToggleId(props.toggleId === 1 ? null : 1)}>
          {props.toggleId === 1 ? "- Hide" : "+ Show"}
        </p>
      </div>

      <Toggler open={props.toggleId === 1} className={classes.Data_Child}>
        <form
          onSubmit={(e) => {
            e.preventDefault();

            uploadHandler((url) => {
              const data = {
                business_name: userData.business_name.value,
                logo: url,
                industry: userData.industry.value,
                website: userData.website.value,
                number: Number(userData.number.value),
              };
              updateGeneralDetail(props.businessId, data)
                .then((data) => {
                  if (data.error) {
                    return setErrorMessage(data.error);
                  } else {
                    setSuccessMessage(data.message);
                    getAllUserBusiness();
                    return fetchDefaultBusiness();
                  }
                })
                .catch((err) => {
                  return setErrorMessage(data.error);
                });
            });
          }}
        >
          {formElementsArray.map((formElement) => {
            if (formElement.config.elementType !== "dropdown") {
              return (
                <Input
                  key={formElement.id}
                  type={formElement.config.elementType}
                  name={formElement.config.type}
                  value={formElement.config.value}
                  inputChangedHandler={inputChangedHandler}
                  required={formElement.config.required}
                  id={formElement.id}
                  placeholder={formElement.config.placeholder}
                  maxLength={formElement.config.maxLength}
                  minLength={formElement.config.minLength}
                />
              );
            } else {
              return (
                <DropDown
                  value={formElement.config.value}
                  key={formElement.id}
                  id={formElement.config.type}
                  handleIndustryValue={handleIndustryValue}
                  list={formElement.config.list}
                />
              );
            }
          })}

          <CustomButton disabled={isDisabled}>Save Changes</CustomButton>
        </form>
      </Toggler>
    </div>
  );
};

